
.home {
  width: 100%;
  min-height: 100vh;

  @media (max-width: 700px) {
    padding: 0;
  }
}

.load-new-posts {
  width: 100%;
  border: 0;
  padding: 9px 0;
  border-radius: 9px;
  margin: 10px auto;
  display: block;

  @media (max-width: 700px) {
    width: calc(100% - 10px);
  }
}

.lists-tab {
  overflow: hidden;
  padding: 0 5px;
  // padding: 10px 0 10px;

  &.sidebar {
    position: absolute;
    margin-top: 10px;
    margin-left: -110px;
    top: 0;

    &.stick-to-top {
      position: fixed;
      margin-top: 12px;
    }

    .list-tab-button {
      width: 100%;
      padding: 10px;
      text-align: center;
      margin: 0 0 10px 0;
      border-radius: 20px;
    }
  }

  @media (max-width: 700px) {
    &.sidebar {
      display: none;
    }
  }
}

.list-tab-button {
  border: none;
  // width: 100%;
  padding: 5px;
  border-radius: 9px;
  background-color: #f5f5f5;
  transition: all 0.1s ease;

  &.active {
    background: #ffd700 !important;
    transform: scale(0.97);
  }
}

.posts {
  padding: 10px 0;
  position: relative;
  z-index: 1;
}

.post {
  background-color: #000;
}

.loadMoreButton {
  display: flex;
  align-items: center;
  height: 44px;
  font-size: 16px;
  cursor: pointer;
  margin: 0 auto 3px;
  border-radius: 8px;
  padding: 0 13px;
  background-color: #fff;
  border: none;
}

.post-loading {
  img {
    width: 45px;
    display: block;
    margin: 14px auto 0;
  }
}
