@import '~src/css/quasar.variables.scss', 'quasar/src/css/variables.sass';

.vue-notification {
  background-color: #0057b7 !important;
}

.post-item-wrapper {
  padding: 20px 20px 1px;
  background-color: rgba(rgb(248, 248, 248), 0.4);
  border-radius: 20px;
  margin: 10px auto 20px;
  transition: background-color 0.3s ease-in-out, padding 0.3s ease-in-out;

  @media (max-width: 650px) {
    border-radius: 0;
    margin: 0 !important;
    padding: 0 0 0.1px !important;
    background-color: transparent !important;
  }
}

.post-item {
  position: relative;
  overflow: hidden;
  margin-bottom: 30px;
  border-radius: 20px;
  border: 1px solid #d7e3ec;
  background: rgb(245, 245, 245);

  .tg-video-widget {
    margin: 0 10px;
  }

  .tgme_widget_message_owner_name {
    text-decoration: none;
  }

  @media (max-width: 768px) {
    // border-radius: 0;
    border: none;

    .content-box.loaded {
      margin: 0 0px 0 0;
    }
  }

  @media (max-width: 650px) {
    border-radius: 0;
  }

  .tgme_widget_message_bubble_tail {
    pointer-events: none;
    position: absolute;
    left: -8px;
    top: -1px;

    &.loaded {
      display: none;
    }
  }

  .head {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    background-color: rgba(#0057b7, 0.1);

    .owner-link {
      display: flex;
      align-items: center;
      text-decoration: none;
      width: 70%;
    }

    .owner-card {
      position: relative;
      width: 30px;
      height: 30px;
      border-radius: 100%;
      overflow: hidden;
      margin-right: 10px;

      .bg-img {
        width: 100%;
        height: 100%;
        z-index: 1;
        position: absolute;
        background-size: cover;
      }

      .bg-placeholder {
        position: absolute;
        padding-top: 100%;
        width: 100%;
        color: #fff;
        filter: blur(4px);

        p {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          font-size: 24px;
          font-weight: 600;
        }
      }
    }

    .author {
      width: calc(100% - 40px);
      font-weight: 700;
      font-size: 16px;
      line-height: 19px;
    }

    .unsubcribe-btn {
      margin-top: -2px;
      font-weight: 400;
      font-size: 12px;
      line-height: 15px;
      /* identical to box height */
      color: #0057b7;
      text-decoration: none;
      cursor: pointer;
      background-color: transparent;
      border: none;
    }
  }

  .imgs-list {
    overflow: hidden;
  }

  .swiper-slide {
    display: flex;
    justify-content: center;
    align-items: center;
    height: auto;
  }

  .swiper-pagination {
    position: absolute;
    left: 0;
    right: 0;
    margin: -40px 0 0 0;
    text-align: center;
    z-index: 1;
    padding: 10px 0;
  }

  .swiper-pagination-bullet {
    width: 10px;
    height: 10px;
    background: #cfcfcf;
    opacity: 0.5;
    border-radius: 100%;
    margin: 0 5px;
    display: inline-block;

    &.swiper-pagination-bullet-active {
      opacity: 1;
    }
  }

  .preview-image {
    width: 100%;
    padding: 0px;
  }

  .content-box {
    opacity: 0;
    pointer-events: none;
    padding: 10px 10px 0;
    transition: all 0.2s linear;

    &.loaded {
      opacity: 1;
      pointer-events: auto;
    }

    .content {
      width: 100%;
      line-height: 26px;
      word-break: break-word;
      font-size: 14px;

      white-space: pre-wrap;

      iframe {
        width: calc(100% + 20px);
        margin: 0 -10px;
      }
    }
  }

  .translated-with {
    font-size: 12px;
    line-height: 17px;
    color: #525252;
    margin-bottom: -2px;

    a {
      font-weight: 600;
      color: #525252;
    }
  }

  .bottom-panel {
    display: flex;
    justify-content: flex-end;
    margin: 10px 36px 0 0;
  }

  a {
    color: #0057b7;
    text-decoration: underline;
  }

  .footer {
    margin: 20px 0 5px;

    &.single-footer {
      padding-bottom: 15px;
    }

    .footer-head {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    .footer-bottom {
      padding: 0 0 16px;

      p {
        font-size: 14px;
        color: #858585;
      }

      .share-title {
        margin-bottom: 8px;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        color: #525252;
      }

      .date {
        text-align: right;
        font-weight: 600;
        font-size: 12px;
        line-height: 17px;
        color: #525252;
        margin-bottom: -2px;
      }

      .footer-box {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;

        .footer-bottom-share-box {
          width: 60%;
        }
      }

      .post-source {
        text-align: right;
        margin-bottom: 8px;
        font-weight: 600;
        font-size: 14px;
        line-height: 17px;
      }

      .a2a_kit {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-wrap: wrap;
      }

      .a2a_svg {
        border-radius: 100%;
        width: 24px;
        height: 24px;
        padding: 3px;
      }

      .a2a_default_style:not(.a2a_flex_style) a {
        display: inline-block;
        padding: 0 6px 0 0;
      }
    }
  }

  iframe {
    min-width: auto !important;
    min-height: 250px;
  }

  .isOlder iframe {
    min-height: 0px;
  }
}

.post-loader-box {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

.post-loader {
  width: 30px;
}

.bg-placeholder {
  padding-top: 100%;
  width: 100%;
  background-color: #0057b7;
  color: #fff;

  p {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 46px;
    font-weight: 600;
  }
}

.comments-link {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 15px 10px;
  text-decoration: none !important;
  font-weight: 700;
  font-size: 12px;
  line-height: 15px;
  color: #525252 !important;
  margin: 0 -10px;
  background-color: rgba(#a2a2a2, 0.1);
  transition: background-color 0.1s linear;

  &:hover {
    background-color: rgba($color: #777777, $alpha: 0.2);
  }

  &:active {
    transform: scale(1);
    filter: brightness(100%);
  }

  // p {
  //   margin-right: 16px;
  // }

  svg {
    margin-left: 4px;
    width: 10px;
  }
}

.copy-posts {
  margin: 10px 0 20px;

  &-show-btn,
  &-hide-btn {
    margin: 0 auto;
    background-color: #e1eaf3;
  }

  &-show-btn {
    margin-top: -10px;
    margin-bottom: 20px;
  }

  &-hide-btn {
    margin-top: -10px;
  }

  .post-item {
    // border-radius: 0;
    // margin-bottom: 0;

    // .footer {
    //   display: none;
    // }
  }

  &-title {
    font-weight: 600;
    font-size: 28px;
    margin-bottom: 10px;
  }
}

.home {
  .copy-posts {
    &-show-btn,
    &-hide-btn {
      @media (max-width: 650px) {
        // width: 100%;
        // border-radius: 0;
        // margin: 0;
        // margin-bottom: -20px;
        // margin-top: -30px;
      }
    }
  }
}
